* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
}

body {
  min-height: 100vh;
}

body,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

.page-footer {
  min-height: 150px;
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #dbdada;
  z-index: -1;
}

